<template>
  <div class="container">
    <!-- Titulo de la pagina en el cuerpo de la pagina -->
    <h1>Laboratorios</h1>
    <!-- Alerta para la pagina -->
    <b-alert
      :show="dismissCountDown"
      dismissible
      variant="warning"
      @dismissed="dismissCountDown=0"
      @dismiss-count-down="countDownChanged"
    >{{mensaje.texto}}</b-alert>
    <!-- Zona en la que se editan los datos del formulario -->
    <form @submit.prevent="editarNota(notaEditar)" v-if="editar">
      <h3>Editar datos</h3>
      <input type="text" class="form-control my-2" placeholder="RUT" v-model="notaEditar.RUT">
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="notaEditar.Nombre">
      <b-button class="btn-warning my-2 mx-2" type="submit">Editar</b-button>
      <b-button class="my-2" type="submit" @click="editar = false">Cancelar</b-button>
    </form>
    <!-- Zona en la que se ingresan datos a un formulario -->
    <form @submit.prevent="agregarNota" v-if="!editar">
      <h3>Ingreso de datos</h3>
      <input type="text" class="form-control my-2" placeholder="RUT" v-model="nota.RUT">
      <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.Nombre">
      <b-button class="bt-success my-2" type="submit">Agregar</b-button>
    </form>
    <!-- Tabla en la que se visualizan los datos de la tabla -->
    <table class="table">
      <thead>
        <tr>
          <th scope="col">ID</th>
          <th scope="col">RUT</th>
          <th scope="col">Nombre</th>
          <th scope="col">Accion</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item, index) in notas" :key="index">
          <th scope="row">{{item._id}}</th>
          <td>{{item.RUT}}</td>
          <td>{{item.Nombre}}</td>
          <td>
            <!-- <b-button @click="alerta">Accion</b-button> -->
            <b-button @click="eliminarNota(item._id)" class="btn-danger btn-sm mx-2">Eliminar</b-button>
            <b-button @click="activarEdicion(item._id)" class="btn-warning btn-sm">Editar</b-button>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      notas: [],
      mensaje: { color: "", texto: "" },
      dismissSecs: 5,
      dismissCountDown: 0,
      nota:{RUT: null, Nombre:''},
      editar: false,
      notaEditar: {}
    };
  },
  created() {
    this.listarNotas();
  },
  methods: {
    alerta() {
      this.mensaje.color = "success";
      this.mensaje.texto = "Alerta";
      this.showAlert();
    },
    listarNotas() {
      this.axios
        .get("/nota")
        .then(res => {
          console.log(res);
          this.notas = res.data;
        })
        .catch(e => {
          console.log(e.response);
        });
    },
    agregarNota(){
      console.log(this.nota);
      this.axios.post('/nueva-nota',this.nota)
      .then(res=>{
        this.notas.push(res.data);
        this.nota.nombre = '',
        this.nota.rut = null;
        this.mensaje.color = 'success';
        this.mensaje.texto = 'Agregado con exito';
        this.showAlert()
      })
      .catch(e=>{
        console.log(e.response);
        if(e.response.data.error.errors.Nombre.message){
          this.mensaje.texto = e.response.data.error.errors.Nombre.message;
        }else if(e.data.error.errors.RUT.message){
          this.mensaje.texto = e.data.error.errors.RUT.message;
        }else{
          this.mensaje.texto = 'Error no especificado';
        }
        this.mensaje.color = 'danger';
        this.showAlert()
      });
    },eliminarNota(id){
      console.log(id);
      this.axios.delete(`/nota/${id}`)
      .then(res=>{
        const index = this.notas.findIndex(item=>item._id === res.data._id);
        this.notas.splice(index, 1);
         this.mensaje.color = 'alert';
        this.mensaje.texto = 'Fila eliminada';
        this.showAlert()
      })
      .catch(e=>{
        console.log(e.response);
      })
    },
    activarEdicion(id){
      this.editar = true;
      console.log(id);
      this.axios.get(`/nota/${id}`)
      .then(res=>{
        this.notaEditar = res.data;
      })
      .catch(e=>{
        console.log(e.response);
      });
    },
    editarNota(item){
      this.axios.put(`/nota/${item._id}`, item)
      .then(res=>{
        const index = this.notas.findIndex(n => n._id === res.data._id);
        this.notas[index].RUT = res.data.RUT;
        this.notas[index].Nombre = res.data.Nombre;
        this.mensaje.color = 'success';
        this.mensaje.texto = 'Fila editada';
        this.showAlert()
        this.editar = false;
      })
      .catch(e=>{
        console.log(e.response);
      });
    },
    countDownChanged(dismissCountDown) {
      this.dismissCountDown = dismissCountDown;
    },
    showAlert() {
      this.dismissCountDown = this.dismissSecs;
    }
  }
};
</script>